@mixin fonts ($font, $style, $weight, $size, $height) {
  font-family: $font;
  font-style: $style;
  font-weight: $weight;
  font-size: $size;
  line-height: $height;
}

@mixin columns($width) {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  >* {
    flex-basis: $width;
  }
}
/* Large screens */
@mixin media-large-screen {
  @media only screen  and (min-width: $large-screen-wrapper-width) {
    @content;
  }
}
