a {
  transition: all 0.5s ease-in-out;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  width: 100%;
  overflow-x: hidden;
  position: relative;
  background-color: #E1D4C9;
}
body.is_block {
  overflow: hidden;
}
body.is_block::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 3;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

h1 {
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 72px;
  line-height: 105%;
  /*
    @include media-mobile {
      @include fonts('Inter', normal, 600, 42px, 115%);
    }*/
}
h1 span.accent {
  font-style: italic;
}

h2 {
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 60px;
  line-height: 125%;
  /*
    @include media-mobile {
      @include fonts('Inter', normal, 600, 32px, 125%);
    }*/
}
h2 span.accent {
  font-style: italic;
}

