@media only screen and (min-width: 1440px) {
  header .wrapper,
  section .wrapper,
  footer .wrapper {
    width: 1440px;
  }
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  /*
  @include media-tablet {
    .wrapper-content {
      width: $tablet-content-width;
    }
  }
  @include media-mobile {
    .wrapper-content {
      width: $mobile-content-width;
    }
  }*/
}
@media only screen and (min-width: 1440px) {
  .wrapper .wrapper-content {
    width: 1360px;
  }
}

.wrapper-content {
  display: flex;
  align-items: center;
}